import React, {Component} from 'react';
import ContentComponent from '../../../components/ContentComponent';
import {getContent} from '../../../fetch/home/content';
import {getNotice} from '../../../fetch/notice/notice';
export  default class Content extends Component {
    constructor() {
        super();
        this.state = {
            data: [],
            notice: '',
            year: 0,
            month: 0,
            strDate: 0,
            day:0
        }
    }
    componentWillMount() {
        let date = new Date();
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        this.setState({
            year,
            month,
            strDate
        })
    }

    componentDidMount() {
        getContent().then(res => res.json()).then(data => {
            this.setState({
                data: data.data
            })
        });
        getNotice().then(res => res.json()).then(data => {
            this.setState({
                notice: data.notice
            })
        });
    }
    displayDaysPerMonth(year) {
        let daysInMonth = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
        if ((year % 4 === 0 && year % 100 !== 0) || year % 400 === 0) {
            daysInMonth[1] = 29
        }
        let daysInPreviousMonth = [].concat(daysInMonth);
        daysInPreviousMonth.unshift(daysInPreviousMonth.pop());
        let addDaysFromPreMonth = new Array(12)
            .fill(null)
            .map((item, index) => {
                let day = new Date(year, index, 1).getDay();
                if (day === 0) {
                    return 6
                } else {
                    return day - 1
                }
            });
        return new Array(12)
            .fill([])
            .map((month, monthIndex) => {
                let addDays = addDaysFromPreMonth[monthIndex],
                    daysCount = daysInMonth[monthIndex],
                    daysCountPrevious = daysInPreviousMonth[monthIndex],
                    monthData = [],
                    preMonthData = [],
                    nextMonthData = [];
                //补足上一个月
                for (; addDays > 0; addDays--) {
                    preMonthData.unshift(daysCountPrevious--)
                }
                //添入当前月
                for (let i = 0; i < daysCount;) {
                    monthData.push(++i)
                }
                //补足下一个月
                for (let i = 42 - monthData.length - preMonthData.length, j = 0; j < i;) {
                    nextMonthData.push(++j)
                }
                return {preMonthData,monthData,nextMonthData}
            })
    }
    //切换到下一个月
    nextMonth() {
        if (this.state.month === 11) {
            this.setState({
                year: ++this.state.year,
                month: 0
            })
        } else {
            this.setState({
                month: ++this.state.month
            })
        }
    }
    //切换到上一个月
    prevMonth() {
        if (this.state.month === 0) {
            this.setState({
                year: --this.state.year,
                month: 11
            })
        } else {
            this.setState({
                month: --this.state.month
            })
        }
    }
    //选择日期
    datePick(day) {
        this.setState({day})
    }
    //切换日期选择器是否显示
    datePickerToggle() {
        this.refs.main.style.height =
            this.refs.main.style.height === '460px' ?
                '0px' : '460px'
    }
    render() {
        let props = {
            viewData: this.displayDaysPerMonth(this.state.year),
            datePicked: `${this.state.year} 年
                   ${this.state.month + 1} 月
                   ${this.state.strDate} 日`
        };
        return (
            <div>
                <ContentComponent data={this.state.data} notice={this.state.notice} year={this.state.year} month={this.state.month} strDate={this.state.strDate} {...props} prevMonth={this.prevMonth.bind(this)} nextMonth={this.nextMonth.bind(this)} datePick={this.datePick.bind(this)} datePickerToggle={this.datePickerToggle.bind(this)}/>
            </div>
        )
    }
}




