import React, {Component} from 'react';
import './index.less';
export  default class CalendarComponent extends Component {
    render() {
        let {year,month,strDate,viewData,prevMonth,nextMonth,datePick,day}={...this.props};
        return (
            <div className="calendar-component">
                <div className="calendar-header">
                    <i className="left" onClick={prevMonth}>&lt;</i><span>{year}年{month}月</span><i className="right" onClick={nextMonth}>&gt;</i>
                </div>
                <div className="week">
                    <span>日</span>
                    <span>一</span>
                    <span>二</span>
                    <span>三</span>
                    <span>四</span>
                    <span>五</span>
                    <span>六</span>
                </div>
                <div className="strDate">
                    {
                        viewData[month].preMonthData.map((item,index)=>(
                            <span key={index} className="other">{item}</span>
                        ))
                    }
                    {
                        viewData[month].monthData.map((item,index)=>(
                            <span key={index} className={strDate==item?'active':''}>{item}</span>
                        ))
                    }
                    {
                        viewData[month].nextMonthData.map((item,index)=>(
                            <span key={index} className="other">{item}</span>
                        ))
                    }
                </div>
            </div>
        )
    }
}



