import React, {Component} from 'react';
import './index.less';
import NoticeComponent from '../NoticeComponent';
import CalendarComponent from '../CalendarComponent';
export  default class RightNavComponent extends Component {
    render() {
        return (
            <div className="right-nav">
                <NoticeComponent notice={this.props.notice}/>
                <CalendarComponent year={this.props.year} month={this.props.month} strDate={this.props.strDate} {...this.props}/>
            </div>
        )
    }
}



