import React, {Component} from 'react';
import DirectoryComponent from '../DirectoryComponent';
import RightNavComponent from '../RightNavComponent';
import './index.less';
export  default class ContentComponent extends Component {
    render() {
        return (
            <div className="content-component">
                {this.props.data.map((item,index)=>(
                <DirectoryComponent data={item} key={index}/>
                ))}
                <RightNavComponent notice={this.props.notice}  year={this.props.year} month={this.props.month} strDate={this.props.strDate} {...this.props}/>
            </div>
        )
    }
}



