import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './index.less';
export  default class HomeHeader extends Component {
    render() {
        return (
            <div className="home-header">
                <div className="center">
                    <Link to="/"><span className={this.props.style=="index"?"active":''}>首页</span></Link>
                    <Link to="/css"><span className={this.props.style=="css"?"active":''}>HTML/CSS</span></Link>
                    <Link to="/js"><span className={this.props.style=="js"?"active":''}>javaScript</span></Link>
                    <Link to="/react"><span className={this.props.style=="react"?"active":''}>react</span></Link>
                    <Link to="/me"><span className={this.props.style=="me"?"active":''}>about me</span></Link>
                </div>
            </div>
        )
    }
}



